.destinations-popup {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: #fff;
  overflow-y: scroll;
}

.destinations-popup::-webkit-scrollbar {
  cursor: pointer;
  width: 5px;
}

.destinations-popup::-webkit-scrollbar-track {
  background: #d9d9d9;
  width: 5px;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  background-clip: content-box;
  border-radius: 18px;
}

.destinations-popup::-webkit-scrollbar-thumb {
  background: #23a8cd;
  cursor: pointer;
  width: 5px;
  max-height: 100px;
  border-radius: 4px;
}

.destinations-popup_inner {
  display: none;
  width: 100%;
  padding: 80px 135px;
  position: relative;
}

.close-destinations {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 48px;
  left: 48px;
}

.close-destinations:before {
  display: block;
  content: '';
  height: 18px;
  width: 2px;
  background-color: #23A8CD;
  position: absolute;
  top: 1px;
  left: 8px;
  transform: rotate(45deg);
}

.close-destinations:after {
  display: block;
  content: '';
  height: 18px;
  width: 2px;
  background-color: #23A8CD;
  position: absolute;
  top: 1px;
  left: 8px;
  transform: rotate(-45deg);
}

.destinations-titles {
  margin-bottom: 55px;
}

.destinations-titles h4 {
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 8px;
}

.destinations-titles p {
  font-size: 17px;
  line-height: 26px;
}

.destinations-items {
  display: flex;
  flex-wrap: wrap;
}

.destination-item {
  width: 32%;
  margin-bottom: 55px;
  margin-right: 21px;
}

.destination-item:nth-child(3n) {
  margin-right: 0;
}

.destination-item:nth-last-child(3), .destination-item:nth-last-child(2), .destination-item:last-child {
  margin-bottom: 0;
}

.destination-item:hover img {
  transform: scale(1.1);
}

.destination-item .thumb-wrap {
  width: 100%;
  height: 320px;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 15px;
}

.destination-item .thumb-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

.destination-item .desctination-description h4 {
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 4px;
}

.destination-item .desctination-description p {
  font-size: 15px;
  line-height: 22px;
}

@media screen and (max-width: 1300px) {
  .destination-item {
    margin-right: 20px;
  }
}

@media screen and (max-width: 1200px) {
  .destinations-popup_inner {
    padding: 80px 50px;
  }
  .destination-item {
    margin-right: 17px;
  }
}

@media screen and (max-width: 900px) {
  .destinations-items {
    justify-content: space-between;
  }
  .destination-item {
    width: 48%;
    margin-right: 0;
  }
}

@media screen and (max-width: 500px) {
  .destinations-popup_inner {
    padding: 50px 20px;
  }
  .close-destinations {
    top: 20px;
    left: 20px;
  }
  .destinations-titles {
    margin-bottom: 30px;
  }
  .destinations-titles h4 {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 4px;
  }
  .destinations-titles p {
    font-size: 16px;
    line-height: 23px;
  }
  .destination-item {
    width: 100%;
    margin-bottom: 30px;
  }
  .destination-item:nth-last-child(3), .destination-item:nth-last-child(2) {
    margin-bottom: 30px;
  }
  .destination-item .thumb-wrap {
    height: 235px;
  }
  .destination-item .desctination-description h4 {
    font-size: 21px;
    line-height: 30px;
  }
  .destination-item .desctination-description p {
    font-size: 14px;
    line-height: 20px;
  }
}

